import axios from 'axios'
import conf from '../config'
import logger from '../logger'
import addStorageForm from '../assets/json/createStorage.json'

const baseUrl = `${conf.apiServer}/api/v1`
axios.defaults.headers.common['Authorization'] = conf.testToken

export default {
  addStorage: function (projectName, storageName, size, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/persistentvolumeclaims`
    addStorageForm.metadata.name = storageName
    addStorageForm.spec.resources.requests.storage = size
    axios.post(url, addStorageForm)
      .then(response => {
        logger.log('[storage]addStorage', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[storage]fail addStorage', error.response)
        _CB(false, error.response.data)
      })
  },
  getStorageList: function (projectName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/persistentvolumeclaims`
    axios.get(url, {})
      .then(response => {
        logger.log('[storage]getStorageList', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[storage]fail getStorageList', error.response)
        _CB(false, error.response.data)
      })
  }
}
