import axios from 'axios'
import conf from '../config'
import logger from '../logger'
import addProjectForm from '../assets/json/addProject.json'
import deleteProjectForm from '../assets/json/deleteProject.json'

axios.defaults.headers.common['Authorization'] = conf.testToken
const baseUrl = `${conf.apiServer}/apis/project.openshift.io/v1`

export default {
  getProjectList: function (_CB) {
    if (conf.useDummyData) {

    } else {
      let url = `${baseUrl}/projects`
      axios.get(url, {})
        .then(response => {
          logger.log('[project]getProjectList', response)
          _CB(true, response.data)
        })
        .catch(function (error) {
          logger.error('[project]fail getProjectList', error.response)
          _CB(false, error.response.data)
        })
    }
  },
  addProject: function (project, _CB) {
    if (conf.useDummyData) {
      setTimeout(() => { _CB(true) }, 500)
    } else {
      let url = `${baseUrl}/projectrequests`
      let form = addProjectForm
      form.metadata.name = project.name
      form.displayName = project.display
      form.description = project.description

      axios.post(url, form)
        .then(response => {
          logger.log('[project]addProject', response)
          _CB(true, response.data)
        })
        .catch(function (error) {
          logger.error('[project]fail addProject', error.response)
          _CB(false, error.response.data)
        })
    }
  },
  deleteProject: function (projectName, _CB) {
    if (conf.useDummyData) {
      setTimeout(() => { _CB(true) }, 500)
    } else {
      let url = `${baseUrl}/projects/` + projectName
      axios.delete(url, { data: deleteProjectForm })
        .then(response => {
          logger.log('[project]deleteProject', response)
          _CB(true)
        })
        .catch(function (error) {
          logger.error('[project]fail deleteProject', error.response)
          _CB(false, error.response.data)
        })
    }
  }
}
