/* eslint prefer-promise-reject-errors: ["error", {"allowEmptyReject": true}] */

import deployApi from '../../api/deploy.js'
import logger from '../../logger'

const state = {
  deployConfigs: { items: [] }
}

const getters = {
  getDeployConfigCount (state) {
    return state.deployConfigs.items.length
  }
}

const actions = {
  getDeployConfigList: function ({ commit }, projectName) {
    return new Promise((resolve, reject) => {
      deployApi.getDeployConfigList(projectName, (result, data) => {
        if (data && result === true) {
          commit('setDeployConfigList', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  createDeployConfig: function ({ commit }, { projectName, form }) {
    return new Promise((resolve, reject) => {
      deployApi.createDeployConfig(projectName, form, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  instantDeploy: function ({ commit }, { projectName, buildName }) {
    return new Promise((resolve, reject) => {
      deployApi.instantDeploy(projectName, buildName, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  editDeployConfig: function ({ commit, state }, { projectName, buildName, paused, form }) {
    return new Promise((resolve, reject) => {
      form.spec.paused = paused
      form.status.latestVersion += 1
      deployApi.editDeployConfig(projectName, buildName, form, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  getDeployConfig: function ({ commit }, { projectName, buildName }) {
    return new Promise((resolve, reject) => {
      deployApi.getDeployConfig(projectName, buildName, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  deleteDeployConfig: function ({ commit }, { projectName, configName }) {
    return new Promise((resolve, reject) => {
      deployApi.deleteBuildConfig(projectName, configName, (result, data) => {
        if (data && result === true) {
          commit('delDeployConfig', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  deleteReplicationController: function ({ commit }, { projectName, deployName }) {
    return new Promise((resolve, reject) => {
      deployApi.deleteReplicationController(projectName, deployName, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  watchDeploy: function ({ commit, dispatch }, projectName) {
    return new Promise((resolve, reject) => {
      logger.log('try abortWatchDeploy=' + projectName)
      deployApi.abortWatchDeploy(projectName)
        .then(response => {
          deployApi.watchDeploy(projectName, (result, data) => {
            if (data && result === true) {
              logger.log('watchDeploy=' + projectName)
              dispatch('getDeployConfigList', projectName)
              resolve(data)
            } else {
              // eslint-disable-next-line prefer-promise-reject-errors
              reject('')
            }
          })
        })
    })
  }
}

const mutations = {
  setDeployConfigList: function (state, configList) {
    state.deployConfigs = configList
  },
  delDeployConfig: function (state, config) {
    const index = state.deployConfigs.items.findIndex(i => {
      return i.metadata.name === config.details.name
    })
    state.deployConfigs.items.splice(index, 1)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
