import axios from 'axios'
import conf from '../config'
import logger from '../logger'

const baseUrl = `${conf.apiServer}/api/v1`
axios.defaults.headers.common['Authorization'] = conf.testToken

export default {
  getControllerList: function (projectName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/replicationcontrollers`
    axios.get(url, {})
      .then(response => {
        logger.log('[controller]getControllerList', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[controller]fail getControllerList', error.response)
        _CB(false, error.response.data)
      })
  },
  getController: function (projectName, controllerName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/replicationcontrollers/` + controllerName
    axios.get(url, {})
      .then(response => {
        logger.log('[controller]getController', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[controller]fail getController', error.response)
        _CB(false, error.response.data)
      })
  },
  deleteReplicationController (projectName, controllerName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/replicationcontrollers/` + controllerName
    axios.delete(url, {})
      .then(response => {
        logger.log('[controller]deleteReplicationController', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        if (error && error.response) {
          logger.error('[controller]fail deleteReplicationController', error.response)
          _CB(false, error.response.data)
        }
      })
  },
  getControllerLog: function (projectName, controllerName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/replicationcontrollers/` + controllerName + `/log`
    axios.get(url, {})
      .then(response => {
        logger.log('[controller]getControllerLog', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[controller]fail getControllerLog', error.response)
        _CB(false, error.response.data)
      })
  }
}
