/* eslint prefer-promise-reject-errors: ["error", {"allowEmptyReject": true}] */

import podApi from '../../api/pod.js'
import logger from '../../logger'

const state = {
  podList: { items: [] }
}

const getters = {
  getPodCount (state) {
    return state.podList.items.length
  }
}

const actions = {
  getPodList: function ({ commit }, projectName) {
    return new Promise((resolve, reject) => {
      podApi.getPodList(projectName, (result, data) => {
        if (data && result === true) {
          commit('setPodList', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  watchPod: function ({ commit, dispatch }, projectName) {
    return new Promise((resolve, reject) => {
      logger.log('try abortWatchPod=' + projectName)
      podApi.abortWatchPod(projectName)
        .then(response => {
          podApi.watchPod(projectName, (result, data) => {
            if (data && result === true) {
              logger.log('watchPod=' + projectName)
              // dispatch('getPodList', projectName)
              resolve(data)
            } else {
              // eslint-disable-next-line prefer-promise-reject-errors
              reject('')
            }
          })
        })
    })
  },
  getPodLog: function ({ commit }, { projectName, podName }) {
    return new Promise((resolve, reject) => {
      podApi.getPodLog(projectName, podName, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  deletePod: function ({ commit }, { projectName, podName }) {
    return new Promise((resolve, reject) => {
      podApi.deletePod(projectName, podName, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  }
}

const mutations = {
  setPodList: function (state, podList) {
    state.podList = podList
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
