import axios from 'axios'
import conf from '../config'
import logger from '../logger'
import instantBuildForm from '../assets/json/instantBuild.json'

const baseUrl = `${conf.apiServer}/apis/build.openshift.io/v1`
axios.defaults.headers.common['Authorization'] = conf.testToken

const controllers = new Map()

export default {
  /**
   * BUILD CONFIG
   */
  getBuildConfigList: function (projectName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/buildconfigs`
    axios.get(url, {})
      .then(response => {
        logger.log('[build]getBuildConfigList', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[build]fail getBuildConfigList', error.response)
        _CB(false, error.response.data)
      })
  },
  createBuildConfig: function (projectName, form, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/buildconfigs`
    axios.post(url, form)
      .then(response => {
        logger.log('[build]createBuildConfig', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[build]fail createBuildConfig', error.response)
        _CB(false, error.response.data)
      })
  },
  deleteBuildConfig: function (projectName, buildName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/buildconfigs/` + buildName
    axios.delete(url, {})
      .then(response => {
        logger.log('[build]deleteBuildConfig', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[build]fail deleteBuildConfig', error.response)
        _CB(false, error.response.data)
      })
  },
  /**
   * BUILD
   */
  instantBuild: function (projectName, buildName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/buildconfigs/` + buildName + `/instantiate`
    instantBuildForm.metadata.name = buildName
    axios.post(url, instantBuildForm)
      .then(response => {
        logger.log('[build]instantBuild', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[build]fail instantBuild', error.response)
        _CB(false, error.response.data)
      })
  },
  abortWatchBuild: async function (projectName) {
    try {
      let control = controllers.get(projectName)
      if (control) {
        control.abort()
      }
    } catch (error) {}
  },
  watchBuild: function (projectName, _CB) {
    let url = `${baseUrl}/watch/namespaces/` + projectName + `/builds`

    let control = new AbortController()
    controllers.set(projectName, control)

    let header = new Headers()
    header.append('Authorization', conf.testToken)
    fetch(url, {
      method: 'get',
      headers: header,
      signal: control.signal
    })
      .then(response => {
        var text = ''
        var reader = response.body.getReader()
        var decoder = new TextDecoder()
        return readChunk()

        function readChunk () {
          return reader.read().then(appendChunks)
        }
        function appendChunks (result) {
          var chunk = decoder.decode(result.value || new Uint8Array(), { stream: !result.done })
          text += chunk
          if (result.done) {
            _CB(true, 'done')
            return text
          } else {
            _CB(true, text)
            text = ''
            return readChunk()
          }
        }
      })
      .then(result => {
        logger.log('[build]watchBuild, done', result)
      })
      .catch(error => {
        logger.error('[build]fail watchBuild', error)
      })
  },
  getBuildList: function (projectName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/builds`
    axios.get(url, {})
      .then(response => {
        logger.log('[build]getBuildList', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[build]fail getBuildList', error.response)
        _CB(false, error.response.data)
      })
  },
  deleteBuild: function (projectName, buildName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/builds/` + buildName
    axios.delete(url, {})
      .then(response => {
        logger.log('[build]deleteBuild', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[build]fail deleteBuild', error.response)
        _CB(false, error.response.data)
      })
  },
  getBuildLog: function (projectName, buildName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/builds/` + buildName + `/log`
    axios.get(url, {})
      .then(response => {
        logger.log('[build]getBuildLog', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[build]fail getBuildLog', error.response)
        _CB(false, error.response.data)
      })
  },
  deleteBuildAllInNamespace: function (projectName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/builds`
    axios.delete(url, {})
      .then(response => {
        logger.log('[build]deleteBuildAllInNamespace', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[build]fail deleteBuildAllInNamespace', error.response)
        _CB(false, error.response.data)
      })
  }
}
