import axios from 'axios'
import conf from '../config'
import logger from '../logger'

const baseUrl = `${conf.apiServer}/apis/image.openshift.io/v1`
axios.defaults.headers.common['Authorization'] = conf.testToken

export default {
  createImageStream: function (projectName, form, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/imagestreams`
    axios.post(url, form)
      .then(response => {
        logger.log('[image]createImageStream', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[image]fail createImageStream', error.response)
        _CB(false, error.response.data)
      })
  },
  getImageStream: function (projectName, buildName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/imagestreams/` + buildName
    axios.get(url, {})
      .then(response => {
        logger.log('[imagestream]getImageStream', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[imagestream]fail getImageStream', error.response)
        _CB(false, error.response.data)
      })
  }
}
