import axios from 'axios'
import conf from '../config'
import logger from '../logger'
import instantDeployForm from '../assets/json/instantDeploy.json'

const baseUrl = `${conf.apiServer}/apis/apps.openshift.io/v1`
const controllerUrl = `${conf.apiServer}/api/v1`
axios.defaults.headers.common['Authorization'] = conf.testToken

const controllers = new Map()

export default {
  getDeployConfigList: function (projectName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/deploymentconfigs`
    axios.get(url, {})
      .then(response => {
        logger.log('[deploy]getDeployConfigList', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[deploy]fail getDeployConfigList', error.response)
        _CB(false, error.response.data)
      })
  },
  createDeployConfig: function (projectName, form, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/deploymentconfigs`
    axios.post(url, form)
      .then(response => {
        logger.log('[deploy]createDeployConfig', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[deploy]fail createDeployConfig', error.response)
        _CB(false, error.response.data)
      })
  },
  instantDeploy: function (projectName, buildName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/deploymentconfigs/` + buildName + `/instantiate`
    instantDeployForm.name = buildName
    axios.post(url, instantDeployForm)
      .then(response => {
        logger.log('[deploy]instantDeploy', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[deploy]fail instantDeploy', error.response)
        _CB(false, error.response.data)
      })
  },
  getDeployConfig: function (projectName, buildName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/deploymentconfigs/` + buildName
    axios.get(url, {})
      .then(response => {
        logger.log('[deploy]getDeployConfig', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[deploy]fail getDeployConfig', error.response)
        _CB(false, error.response.data)
      })
  },
  editDeployConfig: function (projectName, buildName, form, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/deploymentconfigs/` + buildName
    axios.put(url, form)
      .then(response => {
        logger.log('[deploy]editDeployConfig', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[deploy]fail editDeployConfig', error.response)
        _CB(false, error.response.data)
      })
  },
  deleteBuildConfig: function (projectName, configName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/deploymentconfigs/` + configName
    axios.delete(url, {})
      .then(response => {
        logger.log('[deploy]deleteBuildConfig', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[deploy]fail deleteBuildConfig', error.response)
        _CB(false, error.response.data)
      })
  },
  abortWatchDeploy: async function (projectName) {
    try {
      let control = controllers.get(projectName)
      if (control) {
        control.abort()
      }
    } catch (error) {}
  },
  watchDeploy: function (projectName, _CB) {
    let url = `${baseUrl}/watch/namespaces/` + projectName + `/deploymentconfigs`

    let control = new AbortController()
    controllers.set(projectName, control)

    let header = new Headers()
    header.append('Authorization', conf.testToken)
    fetch(url, {
      method: 'get',
      headers: header,
      signal: control.signal
    })
      .then(response => {
        var text = ''
        var reader = response.body.getReader()
        var decoder = new TextDecoder()
        return readChunk()

        function readChunk () {
          return reader.read().then(appendChunks)
        }
        function appendChunks (result) {
          var chunk = decoder.decode(result.value || new Uint8Array(), { stream: !result.done })
          text += chunk
          if (result.done) {
            _CB(true, 'done')
            return text
          } else {
            _CB(true, text)
            text = ''
            return readChunk()
          }
        }
      })
      .then(result => {
        logger.log('[deploy]watchDeploy, done', result)
      })
      .catch(error => {
        logger.error('[deploy]fail watchDeploy', error)
      })
  }
}
