/* eslint prefer-promise-reject-errors: ["error", {"allowEmptyReject": true}] */

import controllerApi from '../../api/controller.js'

const state = {
  controllerList: { items: [] }
}

const getters = {
}

const actions = {
  getControllerList: function ({ commit }, projectName) {
    return new Promise((resolve, reject) => {
      controllerApi.getControllerList(projectName, (result, data) => {
        if (data && result === true) {
          commit('setControllerList', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  getController: function ({ commit }, { projectName, deployName }) {
    return new Promise((resolve, reject) => {
      controllerApi.getController(projectName, deployName, (result, data) => {
        if (data && result === true) {
          commit('setController', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  deleteReplicationController: function ({ commit }, { projectName, deployName }) {
    return new Promise((resolve, reject) => {
      controllerApi.deleteReplicationController(projectName, deployName, (result, data) => {
        if (data && result === true) {
          commit('delController', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  getControllerLog: function ({ commit }, { projectName, deployName }) {
    return new Promise((resolve, reject) => {
      controllerApi.getControllerLog(projectName, deployName, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  }
}

const mutations = {
  setControllerList: function (state, list) {
    state.controllerList = list
  },
  setController: function (state, c) {
    state.controllerList.items.push(c)
  },
  delController: function (state, c) {
    const index = state.controllerList.items.findIndex(i => {
      return i.metadata.name === c.details.name
    })
    state.controllerList.items.splice(index, 1)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
