/* eslint prefer-promise-reject-errors: ["error", {"allowEmptyReject": true}] */
import projectApi from '../../api/project.js'

const state = {
  projectList: { items: [] },
  currentProject: '',
  currentPackage: ''
}

const getters = {
  getProjectCount (state) {
    return state.projectList.items.length
  }
}

const actions = {
  /**
   * Get All Project List
   */
  getProjectList: function ({ commit }) {
    return new Promise((resolve, reject) => {
      projectApi.getProjectList((result, data) => {
        if (data && result === true) {
          commit('setProjectList', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  setCurrentProject: function ({ commit }, projectName) {
    this.dispatch('build/watchBuild', projectName)
    this.dispatch('deploy/watchDeploy', projectName)
    // this.dispatch('pod/watchPod', projectName)
    commit('updateCurrentProject', projectName)
  },
  addProject: function ({ commit }, project) {
    return new Promise((resolve, reject) => {
      projectApi.addProject(project, (result, data) => {
        if (data && result === true) {
          commit('setProject', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  deleteProject: function ({ commit }, projectName) {
    return new Promise((resolve, reject) => {
      projectApi.deleteProject(projectName, (result, data) => {
        if (result) {
          commit('delProject', projectName)
          resolve()
        } else {
          reject(data.message)
        }
      })
    })
  },
  setCurrentPackage: function ({ commit }, packageName) {
    commit('setPackage', packageName)
  }
}

const mutations = {
  updateCurrentProject: function (state, project) {
    state.currentProject = project
  },
  setProjectList: function (state, projectList) {
    state.projectList = projectList
    // @NOTE, 첫번째 프로젝트를 current project로 설정한다.
    if (state.currentPackage === '') {
      if (projectList.items[0]) {
        state.currentProject = projectList.items[0].metadata.name
      }
    }
  },
  setProject: function (state, project) {
    state.projectList.items.push({
      'metadata': project.metadata,
      'sepc': project.spec,
      'status': project.status
    })
  },
  setPackage: function (state, packageName) {
    state.currentPackage = packageName
  },
  delProject: function (state, projectName) {
    const index = state.projectList.items.findIndex(i => {
      return i.metadata.name === projectName
    })
    state.projectList.items.splice(index, 1)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
