/* eslint prefer-promise-reject-errors: ["error", {"allowEmptyReject": true}] */

import templateApi from '../../api/template.js'

const state = {
  templateList: { items: [] }
}

const getters = {
  getTemplateCount (state) {
    return state.templateList.items.length
  }
}

const actions = {
  getTemplateList: function ({ commit }) {
    return new Promise((resolve, reject) => {
      templateApi.getTemplateList((result, data) => {
        if (data && result === true) {
          commit('setTemplateList', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  getTemplate: function ({ commit }, templateName) {
    return new Promise((resolve, reject) => {
      templateApi.getTemplate(templateName, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  }
}

const mutations = {
  setTemplateList: function (state, list) {
    state.templateList = list
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
