import axios from 'axios'
import conf from '../config'
import logger from '../logger'

const baseUrl = `${conf.apiServer}/api/v1`
axios.defaults.headers.common['Authorization'] = conf.testToken

const controllers = new Map()

export default {
  getPodList: function (projectName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/pods`
    axios.get(url, {})
      .then(response => {
        logger.log('[pod]getPodList', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[pod]fail getPodList', error.response)
        _CB(false, error.response.data)
      })
  },
  watchPod: function (projectName, _CB) {
    let url = `${baseUrl}/watch/namespaces/` + projectName + `/pods`
    let control = new AbortController()
    controllers.set(projectName, control)
    let header = new Headers()
    header.append('Authorization', conf.testToken)
    fetch(url, {
      method: 'get',
      headers: header,
      signal: control.signal
    })
      .then(response => {
        var text = ''
        var reader = response.body.getReader()
        var decoder = new TextDecoder()
        return readChunk()

        function readChunk () {
          return reader.read().then(appendChunks)
        }
        function appendChunks (result) {
          var chunk = decoder.decode(result.value || new Uint8Array(), { stream: !result.done })
          text += chunk
          if (result.done) {
            _CB(true, 'done')
            return text
          } else {
            _CB(true, text)
            text = ''
            return readChunk()
          }
        }
      })
      .then(result => {
        logger.log('[pod]watchPod, done', result)
      })
      .catch(error => {
        logger.error('[pod]fail watchPod', error)
      })
  },
  abortWatchPod: async function (projectName) {
    try {
      let control = controllers.get(projectName)
      if (control) {
        control.abort()
      }
    } catch (error) {}
  },
  getPodLog: function (projectName, podName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/pods/` + podName + `/log`
    axios.get(url, {})
      .then(response => {
        logger.log('[pod]getPodLog', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[pod]fail getPodLog', error.response)
        _CB(false, error.response.data)
      })
  },
  deletePod: function (projectName, podName, _CB) {
    let url = `${baseUrl}/namespaces/` + projectName + `/pods/` + podName
    axios.delete(url, {})
      .then(response => {
        logger.log('[pod]deletePod', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[pod]fail deletePod', error.response)
        _CB(false, error.response.data)
      })
  }
}
