/* eslint prefer-promise-reject-errors: ["error", {"allowEmptyReject": true}] */

import storageApi from '../../api/storage.js'

const state = {
  storageList: new Map()
}

const getters = {
}

const actions = {
  addStorage: function ({ commit }, { projectName, storageName, storageSize }) {
    return new Promise((resolve, reject) => {
      storageApi.addStorage(projectName, storageName, storageSize, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  getStorageList: function ({ commit }, projectName) {
    return new Promise((resolve, reject) => {
      storageApi.getStorageList(projectName, (result, data) => {
        if (data && result === true) {
          commit('setStorageList', { projectName, data })
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  }
}

const mutations = {
  setStorageList: function (state, { projectName, data }) {
    state.storageList.set(projectName, data)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
