import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/Login.vue')
  }, {
    path: '/',
    redirect: '/dashboard',
    name: 'HOME',
    component: () => import('../views/container/DefaultContainer.vue'),
    beforeEnter: (to, from, next) => {
      if (store.state.user.isAuth) return next()
      else next('/login')
    },
    children: [
      {
        path: 'Dashboard',
        name: '',
        component: () => import('../views/Dashboard.vue')
      },
      {
        path: ':projectName',
        name: 'main',
        redirect: '/main/:projectName',
        meta: { label: '' },
        component: {
          render (c) { return c('router-view') }
        },
        props: true,
        children: [
          {
            path: ':projectName',
            name: '',
            meta: { label: '' },
            component: () => import('../views/PackageList.vue'),
            props: true
          },
          {
            path: ':packageName',
            name: 'detail',
            meta: { label: '' },
            component: () => import('../views/PackageDetail.vue'),
            props: true
          }
        ]
      },
      /*
      {
        path: ':packageName',
        name: 'detail',
        meta: { label: '' },
        component: () => import('../views/PackageDetail.vue'),
        props: true
      },
      */
      // for test
      {
        path: 'apitest/:projectName',
        name: 'REST API Test',
        component: () => import('../views/APITest.vue'),
        props: true
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
