import userApi from '../../api/user.js'
import logger from '../../logger'

const state = {
  user: {
    usrNm: 'admin',
    user_pwd: 'admin'
  },
  isAuth: true,
  error: null
}

const getters = {
}

const actions = {
  login: function ({ commit }, userInfo) {
    return new Promise((resolve, reject) => {
      userApi.login(userInfo, (ret, error) => {
        logger.log('login store', ret, error)
        if (ret && ret.code === 'C000') {
          // 성공시 {message: "OK", status: 200, code: "C000"}
          commit('setIsAuth', true)
          commit('setError', null)
          resolve(true)
        } else {
          commit('setIsAuth', false)
          commit('setError', error)
          reject(error)
        }
      })
    })
  },
  getUser: function ({ commit }) {
    return new Promise((resolve, reject) => {
      userApi.getCurrentUser((user, error) => {
        logger.log('login store', user, error)
        resolve(user)
        commit('setLoginUser', user)
      })
    })
  },
  logout: function ({ commit }) {
    return new Promise((resolve, reject) => {
      userApi.logout(() => {
        resolve(null)
        commit('setLoginUser', null)
      })
    })
  }
}

const mutations = {
  setLoginUser: function (state, user) {
    state.user = user
  },
  setIsAuth: function (state, isAuth) {
    state.isAuth = isAuth
  },
  setError: function (state, error) {
    state.error = error
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
