import axios from 'axios'
import conf from '../config'
import logger from '../logger'

const baseUrl = `${conf.apiServer}/apis/template.openshift.io/v1/namespaces/openshift/templates`
axios.defaults.headers.common['Authorization'] = conf.testToken

export default {
  getTemplateList: function (_CB) {
    let url = baseUrl
    axios.get(url, {})
      .then(response => {
        logger.log('[template]getTemplateList', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[template]fail getTemplateList', error.response)
        _CB(false, error.response.data)
      })
  },
  getTemplate: function (templateName, _CB) {
    let url = baseUrl + '/' + templateName
    axios.get(url, {})
      .then(response => {
        logger.log('[template]getTemplate', response)
        _CB(true, response.data)
      })
      .catch(function (error) {
        logger.error('[template]fail getTemplate', error.response)
        _CB(false, error.response.data)
      })
  }
}
