/* eslint prefer-promise-reject-errors: ["error", {"allowEmptyReject": true}] */

import imagestreamApi from '../../api/imagestream.js'

const state = {
}

const getters = {
}

const actions = {
  createImageStream: function ({ commit }, { projectName, form }) {
    return new Promise((resolve, reject) => {
      imagestreamApi.createImageStream(projectName, form, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  getImageStream: function ({ commit }, { projectName, buildName }) {
    return new Promise((resolve, reject) => {
      imagestreamApi.getImageStream(projectName, buildName, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  }
}

const mutations = {
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
