
/* eslint-disable no-unused-vars */
import axios from 'axios'
import conf from '../config'
import logger from '../logger'

axios.defaults.withCredentials = false

function applyTokenDisplay (response) {
  var el = document.createElement('html')
  el.innerHTML = response
  var formData = el.getElementsByTagName('form')
  let formToServer = ''
  let csrf = ''
  formData.forEach(f => {
    logger.log(f.method)
    logger.log(f.action)
    logger.log(f.csrf.value)
    logger.log(f.then.value)
    csrf = f.csrf.value
    formToServer = 'then=' + f.then.value
    formToServer += '&csrf=' + f.csrf.value
    formToServer += '&username=admin^&password=admin'
    axios.defaults.headers.common['X-CSRF-TOKEN'] = f.csrf.value
    axios.defaults.headers.common['then'] = f.then.value
    axios.defaults.headers.common['content-type'] = 'application/x-www-form-urlencoded'
  })
  axios.post(`${conf.apiServer}/login`, formToServer, {
    headers: {
      'X-Csrf-Token': csrf,
      'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET,POST,PUT,DELETE,OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type, Authorization, Content-Length, X-Requested-With'
    }
  })
    .then(response => {
      logger.log('response : ', JSON.stringify(response, null, 2))
    })
    .catch(error => { logger.log('failed', error) })
  return 'token'
}

export default {
  login: function (user, _CB) {
    var basicUrl = `${conf.apiServer}`
    let redirectUrl = '&https://192.168.1.3:8443/oauth/token/display'
    // let redirectUrl = '&https://www.rosdev.io:8443/console/oauth'
    var url = basicUrl + `/oauth/authorize?response_type=code&client_id=ros2-webui` + redirectUrl
    axios.get(url, {}, {
      headers: {
        'X-Csrf-Token': '1',
        'content-type': 'application/x-www-form-urlencoded'
      }
    }).then(response => {
      let token = applyTokenDisplay(response.data)
      logger.log('[user]login', token)
      _CB(true, token)
    }).catch(function (error) {
      logger.error('[user]fail login', error)
      _CB(false, error)
    })
  },
  logout: function (_CB) {
  },
  getCurrentUser: function (_CB) {
  }
}
