/* eslint prefer-promise-reject-errors: ["error", {"allowEmptyReject": true}] */

import buildApi from '../../api/build.js'
import logger from '../../logger'

const state = {
  buildConfigs: { items: [] },
  buildList: { items: [] }
}

const getters = {
  getBuildConfigCount (state) {
    return state.buildConfigs.items.length
  },
  getBuildCount (state) {
    return state.buildConfigs.items.length
  }
}

const actions = {
  getBuildConfigList: function ({ commit }, projectName) {
    return new Promise((resolve, reject) => {
      buildApi.getBuildConfigList(projectName, (result, data) => {
        if (data && result === true) {
          commit('setBuildConfigList', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  createBuildConfig: function ({ commit }, { projectName, form }) {
    return new Promise((resolve, reject) => {
      buildApi.createBuildConfig(projectName, form, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  deleteBuildConfig: function ({ commit }, { projectName, configName }) {
    return new Promise((resolve, reject) => {
      buildApi.deleteBuildConfig(projectName, configName, (result, data) => {
        if (data && result === true) {
          commit('delBuildConifgFromList', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  instantBuild: function ({ commit }, { projectName, buildName }) {
    return new Promise((resolve, reject) => {
      buildApi.instantBuild(projectName, buildName, (result, data) => {
        if (data && result === true) {
          // return build, refersh build and buildConfig
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  watchBuild: function ({ commit, dispatch }, projectName) {
    return new Promise((resolve, reject) => {
      logger.log('try abortWatchBuild=' + projectName)
      buildApi.abortWatchBuild(projectName)
        .then(response => {
          buildApi.watchBuild(projectName, (result, data) => {
            if (data && result === true) {
              logger.log('watchBuild=' + projectName)
              dispatch('getBuildList', projectName)
              resolve(data)
            } else {
              // eslint-disable-next-line prefer-promise-reject-errors
              reject('')
            }
          })
        })
    })
  },
  abortWatchBuild: function ({ commit }, projectName) {
    logger.log('abortWatchBuild=' + projectName)
    buildApi.abortWatchBuild(projectName)
  },
  getBuildList: function ({ commit }, projectName) {
    return new Promise((resolve, reject) => {
      buildApi.getBuildList(projectName, (result, data) => {
        if (data && result === true) {
          commit('setBuildList', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  deleteBuild: function ({ commit }, { projectName, buildName }) {
    return new Promise((resolve, reject) => {
      buildApi.deleteBuild(projectName, buildName, (result, data) => {
        if (data && result === true) {
          commit('delBuildFromList', data)
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  getBuildLog: function ({ commit }, { projectName, buildName }) {
    return new Promise((resolve, reject) => {
      buildApi.getBuildLog(projectName, buildName, (result, data) => {
        if (data && result === true) {
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  },
  deleteBuildAllInNamespace: function ({ commit }, projectName) {
    return new Promise((resolve, reject) => {
      buildApi.deleteBuildAllInNamespace(projectName, (result, data) => {
        if (data && result === true) {
          commit('setBuildList', { items: [] })
          resolve(data)
        } else {
          reject(data.message)
        }
      })
    })
  }
}

const mutations = {
  setBuildConfigList: function (state, configList) {
    state.buildConfigs = configList
  },
  setBuildList: function (state, buildList) {
    state.buildList = buildList
  },
  updateWatch: function (state, flag, version) {
    state.buildWatch.enable = flag
    state.buildWatch.version = version
  },
  delBuildFromList: function (state, build) {
    const index = state.buildList.items.findIndex(i => {
      return i.metadata.name === build.details.name
    })
    state.buildList.items.splice(index, 1)
  },
  delBuildConifgFromList: function (state, build) {
    const index = state.buildConfigs.items.findIndex(i => {
      return i.metadata.name === build.details.name
    })
    state.buildConfigs.items.splice(index, 1)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
